import React, { useContext } from 'react'
import './pager.scss'
import {
  Context,
  UIR_DECREMENT_PAGER,
  UIR_INCREMENT_PAGER,
} from '../../store/UIStore'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faAngleDoubleLeft,
  faAngleDoubleRight,
} from '@fortawesome/free-solid-svg-icons'

function Pager(props) {
  const [state, dispatch] = useContext(Context)

  const setPageEvent = e => {
    let newPage = e.target.getAttribute('data-index')
    props.setPageCB(newPage)
  }

  const getPage = i => {
    return (
      <div
        className={
          i == state[props.curPage]
            ? 'page-block page-highlighted'
            : 'page-block'
        }
        data-index={i}
        key={'pageKey-' + i}
        onClick={e => setPageEvent(e)}
      >
        {i + 1}
      </div>
    )
  }

  const decrPager = () => {
    dispatch({
      type: UIR_DECREMENT_PAGER,
      payload: {
        curPage: props.curPage,
        startPage: props.startPage,
      },
    })
  }

  const incrPager = () => {
    dispatch({
      type: UIR_INCREMENT_PAGER,
      payload: {
        curPage: props.curPage,
        startPage: props.startPage,
        pageSize: props.pageSize,
      },
    })
  }

  const getPages = () => {
    let pages = []

    if (state[props.pageSize] < 2) {
      return pages
    }

    let pSize = state[props.pageSize] <= 4 ? state[props.pageSize] - 2 : 3

    pages.push(getPage(0))

    for (
      let i = state[props.startPage];
      i < state[props.startPage] + pSize;
      i++
    ) {
      if (state[props.startPage] > 1 && i == state[props.startPage]) {
        pages.push(
          <FontAwesomeIcon
            className="page-icon"
            key={'leftPage' + i}
            icon={faAngleDoubleLeft}
            onClick={() => decrPager()}
          ></FontAwesomeIcon>
        )
      }
      pages.push(getPage(i))
    }

    if (state[props.startPage] < state[props.pageSize] - 4) {
      pages.push(
        <FontAwesomeIcon
          className="page-icon"
          key={'rightPage'}
          icon={faAngleDoubleRight}
          onClick={() => incrPager()}
        ></FontAwesomeIcon>
      )
    }

    pages.push(getPage(state[props.pageSize] - 1))
    return pages
  }

  return (
    <div className="pager-view">
      <div className="paginator">{getPages()}</div>
    </div>
  )
}

export default Pager
