import React, { useContext } from 'react'
import { useHistory } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faAngleDoubleLeft,
  faAngleDoubleRight,
  faAngleUp,
  faAngleDown,
} from '@fortawesome/free-solid-svg-icons'
import './blockView.scss'
import { getBlocks } from '../../utils/Api'
import {
  Context,
  UIR_CUR_BLOCK,
  UIR_CUR_BLOCK_PAGE,
  UIR_START_BLOCK_PAGE,
  UIR_BLOCK_LIST,
  UIR_BLOCK_PAGE_SIZE,
  UIR_DECREMENT_PAGER,
  UIR_INCREMENT_PAGER,
} from '../../store/UIStore'
import Pager from '../../components/pager/Pager'
import blockImg from '../../images/by_block.jpg'
import chain from '../../images/chain.png'

function BlockView() {
  const [state, dispatch] = useContext(Context)
  const history = useHistory()
  const curBlockRef = React.useRef(null)
  const blockViewRef = React.useRef(null)
  const unused = 0

  //eslint-disable-next-line
  const setPage = newPage => {
    dispatch({ type: UIR_CUR_BLOCK, payload: 0 })
    dispatch({ type: UIR_CUR_BLOCK_PAGE, payload: newPage })
    getBlocks(
      blocks => {
        dispatch({ type: UIR_BLOCK_LIST, payload: blocks })
      },
      pageSize => {
        dispatch({ type: UIR_BLOCK_PAGE_SIZE, payload: pageSize })
      },
      newPage
    )
  }

  const getBlockDisplay = (b, i) => {
    return (
      <div
        className="block-chain"
        key={'key-' + i}
        ref={b.id == state[UIR_CUR_BLOCK] ? curBlockRef : null}
      >
        <div className="block-container">
          <div>
            <img className="block-image" src={blockImg} alt="block" />
          </div>
          <div className="info-container">
            <FontAwesomeIcon
              className="icon-down"
              icon={faAngleDown}
              size="2x"
              onClick={() => {
                dispatch({ type: UIR_CUR_BLOCK, payload: b.id })
                history.push('/blockDetail/' + b.id)
              }}
            />
            <div>{b.timestamp}</div>
            <div>Number of invoices: {b.numInvoices}</div>
            <div>Amount: {b.amount}</div>
          </div>
        </div>
        <img className="chain-image" src={chain} alt="chain" />
      </div>
    )
  }

  const decrPage = () => {
    if (state[UIR_CUR_BLOCK_PAGE] > 0) {
      if (state[UIR_CUR_BLOCK_PAGE] == state[UIR_START_BLOCK_PAGE]) {
        dispatch({
          type: UIR_DECREMENT_PAGER,
          payload: {
            curPage: UIR_CUR_BLOCK_PAGE,
            startPage: UIR_START_BLOCK_PAGE,
          },
        })
      }

      setPage(state[UIR_CUR_BLOCK_PAGE] - 1)
    }
  }

  const incrPage = () => {
    if (state[UIR_CUR_BLOCK_PAGE] < state[UIR_BLOCK_PAGE_SIZE] - 1) {
      if (state[UIR_CUR_BLOCK_PAGE] == state[UIR_START_BLOCK_PAGE] + 2) {
        dispatch({
          type: UIR_INCREMENT_PAGER,
          payload: {
            curPage: UIR_CUR_BLOCK_PAGE,
            startPage: UIR_START_BLOCK_PAGE,
            pageSize: UIR_BLOCK_PAGE_SIZE,
          },
        })
      }

      setPage(state[UIR_CUR_BLOCK_PAGE] + 1)
    }
  }

  // When the block list is reset, reset the page to 0.
  React.useEffect(() => {
    if (state[UIR_BLOCK_LIST].length == 0) {
      setPage(0)
    }
    //eslint-disable-next-line
  }, [])

  // When the current block is selected, scroll to it. When a new page is selected,
  // scroll the view all the way to the left.
  React.useEffect(() => {
    if (curBlockRef && curBlockRef.current) {
      curBlockRef.current.scrollIntoView({ inline: 'start' })
    } else {
      blockViewRef.current.scrollLeft = 0
    }
  }, [state[UIR_CUR_BLOCK]])

  return (
    <div className="block-view">
      <div className="up-icon">
        <FontAwesomeIcon icon={faAngleUp} size="3x" />
      </div>
      <div className="page-view">
        <FontAwesomeIcon
          className={
            state[UIR_CUR_BLOCK_PAGE] == 0
              ? 'block-chain-page-icon bc-page-icon-disabled'
              : 'block-chain-page-icon'
          }
          onClick={() => decrPage()}
          icon={faAngleDoubleLeft}
          size="3x"
        />
        <div className="block-panel" ref={blockViewRef}>
          {state[UIR_BLOCK_LIST].map((b, i) => getBlockDisplay(b, i))}
        </div>
        <FontAwesomeIcon
          className={
            state[UIR_CUR_BLOCK_PAGE] == state[UIR_BLOCK_PAGE_SIZE] - 1
              ? 'block-chain-page-icon bc-page-icon-disabled'
              : 'block-chain-page-icon'
          }
          onClick={() => incrPage()}
          icon={faAngleDoubleRight}
          size="3x"
        />
      </div>
      <div className="paginator-panel">
        <Pager
          curPage={UIR_CUR_BLOCK_PAGE}
          setPageCB={setPage}
          startPage={UIR_START_BLOCK_PAGE}
          pageSize={UIR_BLOCK_PAGE_SIZE}
        />
      </div>
    </div>
  )
}

export default BlockView
