import sampleData from './SampleData'

const pageSize = 20

export const getBlocks = async (setBlocks, setNumPages, page = 0) => {
  try {
    setNumPages(Math.ceil(sampleData.length / pageSize))
    let blocks = []
    sampleData.slice(page * pageSize, page * pageSize + pageSize).map((b, i) =>
      blocks.push({
        id: page * pageSize + i,
        timestamp: b.timestamp,
        numInvoices: b.invoices.length,
        amount: b.amount,
      })
    )

    setBlocks(blocks)
  } catch (e) {
    console.log('Something went wrong')
  }
}

export const getInvoices = async (blockId, setInvoices) => {
  try {
    let bid = parseInt(blockId)
    let invoices = []
    let invList = sampleData[bid].invoices
    invList.map(i =>
      invoices.push({
        id: i.id,
        amount: i.amount,
        creationTime: i.date_info.creation,
        vendor: i.vendor.name,
        buyer: i.buyer.name,
      })
    )
    setInvoices(invoices)
  } catch (e) {
    console.log('Error in getting invoices')
  }
}

export const getDays = (setDays, setNumPages, page = 0) => {
  var d = new Date()
  d.setDate(d - page)
  let dayData = []

  for (var i = 0; i < pageSize; i++) {
    let dateParts = d.toDateString().split(' ')
    dayData.push({
      time: dateParts[3] + ' ' + dateParts[1] + ' ' + dateParts[2],
      numBlocks: Math.floor(Math.random() * Math.floor(50)),
    })
    d.setDate(d - 1)
  }

  return dayData
}
