import React from 'react'
import { useHistory } from 'react-router-dom'
import { useParams } from 'react-router'
//import { Context, UIR_CUR_BLOCK } from '../../store/UIStore'
import { getInvoices } from '../../utils/Api'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleUp } from '@fortawesome/free-solid-svg-icons'
import './invoiceView.scss'

function InvoiceView() {
  const [invoices, setInvoices] = React.useState([])
  let { id } = useParams()
  const history = useHistory()
  //  const [state, dispatch] = useContext(Context)

  React.useEffect(() => {
    getInvoices(id, setInvoices)
    //eslint-disable-next-line
  }, [])

  React.useEffect(() => {
    console.log('invoices = ' + JSON.stringify(invoices))
  }, [invoices])

  return (
    <div className="invoice-view">
      <div className="up-icon">
        <FontAwesomeIcon
          icon={faAngleUp}
          size="3x"
          onClick={() => {
            history.push('/')
          }}
        />
      </div>
      {invoices.length === 0 && <div>No Invoices in this Block</div>}
      {invoices.length > 0 && (
        <div>
          <h3 className="invoice-title">Invoices for Block {id}</h3>
          <table className="invoice-table">
            <thead>
              <tr>
                <th>Vendor</th>
                <th>Buyer</th>
                <th>Creation Time</th>
                <th>Amount</th>
              </tr>
            </thead>
            <tbody>
              {invoices.map((inv, i) => (
                <tr key={'inv-key' + i}>
                  <td>{inv.vendor}</td>
                  <td>{inv.buyer}</td>
                  <td>{inv.creationTime}</td>
                  <td>{inv.amount}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  )
}

export default InvoiceView
